







import { Vue, Component } from "vue-property-decorator";
import WalletFaqContent from "@/components/misc/WalletFaqContent.vue";
@Component({
  components: { WalletFaqContent },
})
export default class Walletfaq extends Vue {
  get isCore() {
    let urlParams = new URLSearchParams(window.location.search);
    if (urlParams.has("core")) {
      return true;
    }
    return false;
  }
}
